

import "./Main.css"
import hello from "../../assets/logo.png";
import admin from "../../assets/admin-image-1.jpg";
import Charts from "../charts/Charts";
const Main = () => {
    return (
        <main>
            <div className="main__container">
                <div className="main__title">
                    <img src={admin} alt="admin-image" />
                    <div className="main__greeting">
                        <h1>Hello Faizan</h1>
                        <p>Welcome to your admin dashboard.</p>
                    </div>
                </div>
                <div className="main__cards">
                    <div className="card">
                        <i className="fa fa-user-o fa-2x text-lightblue"></i>
                        <div className="card_inner">
                            <p className="text-primary-p">Number of Subscribers</p>
                            <span className="font-bold text-title">578</span>
                        </div>
                    </div>

                    <div className="card">
                        <i className="fa fa-user-o fa-2x text-red"></i>
                        <div className="card_inner">
                            <p className="text-primary-p">Times of Watching</p>
                            <span className="font-bold text-title">5578</span>
                        </div>
                    </div>

                    <div className="card">
                        <i className="fa fa-video-camera fa-2x text-yellow"></i>
                        <div className="card_inner">
                            <p className="text-primary-p">Number of Videos</p>
                            <span className="font-bold text-title">578</span>
                        </div>
                    </div>

                    <div className="card">
                        <i className="fa fa-thumbs-up fa-2x text-green"></i>
                        <div className="card_inner">
                            <p className="text-primary-p">Number of Likes </p>
                            <span className="font-bold text-title">3578</span>
                        </div>
                    </div>
                </div>
                <div className="charts">
                    <div className="charts__left">
                        <div className="charts__left__title">
                            <div>
                                <h1>Daily Reports</h1>
                                <p>Cupertino, CA, USA</p>
                            </div>
                            <i className="fa fa-usd"></i>
                        </div>
                        <Charts/>
                    </div>
                    <div className="charts__right">
                        <div className="charts__right__title">
                            <div>
                                <h1>Stats Report</h1>
                                <p>Cupertino, CA, USA</p>
                            </div>
                            <i className="fa fa-usd"></i>
                        </div>
                        <div className="charts__right__card">
                            <div className="card1">
                                <h1>Income</h1>
                                <p>$75,300</p>
                            </div>
                            <div className="card2">
                                <h1>Sales</h1>
                                <p>$5,300</p>
                            </div>
                            <div className="card3">
                                <h1>Users</h1>
                                <p>$7300</p>
                            </div>
                            <div className="card4">
                                <h1>Orders</h1>
                                <p>$75,00</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </main>
    );
}

export default Main;
